.acf-block-preview .page-banner {
  pointer-events: none;
  height: 400px;
}
.acf-block-preview .breadcrumbs-container {
  display: none;
}

.page-banner {
  background: #eee;
  height: 600px;
  position: relative;
}
.page-banner .page_banner-background {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.page-banner .page_banner-background img, .page-banner .page_banner-background video {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.page-banner .page_banner-content {
  position: absolute;
  z-index: 3;
  bottom: 0;
  height: 400px;
  width: 100%;
  padding: 0 20px 30px;
  background: black;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: flex-end;
  -ms-flex-align: flex-end;
  -webkit-align-items: flex-end;
  align-items: flex-end;
  -webkit-justify-content: space-between;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -moz-flex-direction: row;
  -webkit-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
.page-banner .page_banner-content-title {
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-size: 4.25rem;
  color: #fefefe;
  line-height: 1;
  margin-bottom: 5px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.page-banner .page_banner-content-subtitle {
  font-size: 1.5rem;
  color: #fefefe;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.page-banner:hover .page_banner-content {
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}
.page-banner:hover .page_banner-content-title, .page-banner:hover .page_banner-content-subtitle {
  -webkit-transform: translateY(-10px);
      -ms-transform: translateY(-10px);
          transform: translateY(-10px);
}

@media print, screen and (min-width: 40em) {
  .page-banner .page_banner-content {
    padding: 0 120px 30px;
  }
}